import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default {
	components: {
		uniPopup,
	},
	data() {
		return {
			show: false,
			type: 'center',
			title: '会员卡首页',
			balanceMoney:1000,
			payMoneyList:[
				{quotaMoney:50,give:3},
				{quotaMoney:100,give:10},
				{quotaMoney:200,give:25},
				{quotaMoney:500,give:70},
				{quotaMoney:800,give:115},
				{quotaMoney:1000,give:150},
				{quotaMoney:1500,give:230},
				{quotaMoney:2000,give:320},
				{quotaMoney:3000,give:500},
			]
		}
	},
	onLoad() {

	},
	methods: {
		togglePopup(open) {
			if (open === 'tip') {
				this.show = true
			} else {
				this.$refs[open].open()
			}
		},
		cancel(type) {
			if (type === 'tip') {
				this.show = false
				return
			}
			this.$refs[type].close()
		},
		change(e) {
			console.log(e.show)
		},
		goPaySuccess(param){
			console.log(param)
			uni.navigateTo({
				url: './paySuccess?money='+param.quotaMoney+'&give='+param.give,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}
